import React, { Fragment, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import Login from './Login'
import Signup from './Signup'
import { authError, authModalOpen } from '../../state/actions/layout'

const AuthModal = () => {
    const [login, setLogin] = useState(true)

    const dispatch = useDispatch()
    const data = useSelector(state => state.layout)

    const closeAuthModal = () => {
        dispatch(authError(null))
        dispatch(authModalOpen(false))
    }

    return (
        <Fragment>
            {/* Black Overlay  */}
            <div onClick={closeAuthModal} className={`${data.authModal ? "" : "hidden"} fixed top-0 z-40 w-full h-screen bg-black opacity-50 cursor-pointer`}></div>
            {/* Signup Login Component Render */}
            <section className={`${data.authModal ? "" : "hidden"} fixed z-40 inset-0 my-8 md:my-20 flex items-start justify-center overflow-auto`}>
                <div className="w-11/12 md:w-3/5 lg:w-2/4 relative space-y-4 bg-white p-6 md:px-12 md:py-6">
                    {login ? <Login /> : <Signup setLogin={setLogin} />}
                    <div className="flex items-center space-x-2">
                        <span className="border-b border-gray-500 w-full" />
                        <span className="font-medium">or</span>
                        <span className="border-b border-gray-500 w-full" />
                    </div>
                    <div onClick={() => setLogin(!login)} style={{ color: "#303031", border: "1px solid #303031" }} className="px-4 py-2 font-medium text-center cursor-pointer" >
                        {!login ? 'Login' : 'Create an account'}
                    </div>
                    {/*  Modal Close Button */}
                    <div className="absolute top-0 right-0 mx-4">
                        <svg onClick={closeAuthModal} className="w-6 h-6 cursor-pointer" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
                            <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                        </svg>
                    </div>
                </div>
            </section>
        </Fragment>
    )
}

export default AuthModal
