// LIBRARIES
import React, { useEffect } from 'react'
import { BrowserRouter, Routes, Route, Navigate as Redirect } from 'react-router-dom'
import { useDispatch } from 'react-redux'

// ACTIONS
import { currentUser } from './state/actions/layout'
import { USER_LOADING } from './state/constants/layout'
import { Home } from './components/Home'
import { UserWrapper } from './components/user/wrapper'

const AppRoutes = () => {
    const dispatch = useDispatch()

    dispatch({ type: USER_LOADING, payload: true })

    useEffect(() => {
        dispatch(currentUser())
    }, [dispatch])
    console.log(process.env);

    return (
        <UserWrapper>
            <BrowserRouter>
                <Routes>
                    <Route path={`/${process.env.REACT_APP_HOME_PAGE}`} Component={Home} />
                    <Route path='*' element={<Redirect to={`/${process.env.REACT_APP_HOME_PAGE}`} />} />
                </Routes>
            </BrowserRouter>
        </UserWrapper>
    )
}

export default AppRoutes